import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { Container, Stack, Button } from '@mui/material';

const GoWasmComponent: React.FC = () => {
    const [rawStr, setRawStr] = useState("")
    const [encodedStr, setEncodedStr] = useState("")

    function goEncode() {
        return setEncodedStr((window.functions.encode!)(rawStr))
    }

    function goDecode() {
        return setRawStr((window.functions.decode!)(encodedStr))
    }

    return (
        <div>
            <Container fixed>
                <Stack spacing={3}>
                    <Stack spacing={2}>
                        <TextField label="Encode String" variant="outlined" value={rawStr} onChange={(e: React.ChangeEvent<HTMLInputElement>) => { setRawStr(e.target.value) }} />
                        <Button variant="contained" onClick={goEncode}>Encode Base64</Button>
                    </Stack>

                    <Stack spacing={2}>
                        <TextField label="Decode String" variant="outlined" value={encodedStr} onChange={(e: React.ChangeEvent<HTMLInputElement>) => { setEncodedStr(e.target.value) }} />
                        <Button variant="contained" onClick={goDecode}>Decode Base64</Button>
                    </Stack>
                </Stack>
            </Container>
        </div>
    );
}

export default GoWasmComponent;